/* src/components/Contact.css */

.contact {
  padding: 2rem;
}

h1 {
  color: #1abc9c;
  text-align: center;
  margin-bottom: 2rem;
}

.contact-card {
  background-color: #2c3e50; /* Ajustar el color de fondo para que coincida con el resto de la página */
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1.5rem;
  color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.contact-card h2 {
  color: #1abc9c;
  margin-bottom: 0.5rem;
}

.contact-info {
  margin-bottom: 1rem;
}

.contact-info p {
  font-size: 1.2em;
  margin-bottom: 10px;
}

.contact-info a {
  color: #1abc9c;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

.contact-info svg {
  margin-right: 10px;
  color: #1abc9c;
}

.button-container {
  display: flex;
  justify-content: center;
}

.open-modal-btn {
  background-color: #1abc9c;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.2em;
  margin-top: 10px;
}

.open-modal-btn:hover {
  background-color: #16a085;
}

.modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh; /* Cambiar a 100vh para asegurar que se vea el formulario completo */
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modal-content {
  background-color: #2c3e50;
  padding: 20px;
  border-radius: 8px;
  width: 100%;
  max-width: 600px;
  position: relative;
  max-height: 80vh; /* Asegurar que el contenido del modal se ajuste a la pantalla */
  overflow-y: auto; /* Añadir desplazamiento si el contenido es demasiado alto */
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.5em;
  color: white;
  cursor: pointer;
}

iframe {
  width: 100%;
  height: 500px;
  border: none;
  overflow: hidden;
  border-radius: 8px;
}